.project {
  width: 80%;
  min-width: 300px;
  border-radius: 10px;
  margin-top: 50px;
  background: #fcf3ca;
}

.project-title {
  margin-left: 20px;
}

.project:last-of-type {
  margin-bottom: 50px;
}

.project-img {
  width: 100%;
  border-top: 1px solid darkgray;
  border-bottom: 1px solid darkgray;
}

.project-description {
  padding: 0 20px;
  margin: 20px 0 0 0;
}

.project-link-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 20px;
}

.project-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.project-link-name {
  margin: 0;
  margin-top: 15px;
  font-weight: 600;
}

.project-link {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px;
  border: none;
  margin: 10px 20px 10px 20px;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  background-color: #c4eada;
  -webkit-box-shadow: 4px 4px 0 0 #ef7b7b;
          box-shadow: 4px 4px 0 0 #ef7b7b;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.project-link:hover {
  -webkit-transform: translateY(4px) translateX(4px);
      -ms-transform: translateY(4px) translateX(4px);
          transform: translateY(4px) translateX(4px);
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
  background-color: #ef7b7b;
  -webkit-box-shadow: -4px -4px 0 0 #c4eada;
          box-shadow: -4px -4px 0 0 #c4eada;
}



